@import '../src/css/themes/theme-dark.css';
@import '../src/css/themes/theme-light.css';
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@200;300&display=swap');


html {
	 font-size: 62.5%;
	 -webkit-font-smoothing: antialiased;
	 margin: 0;
	 padding: 0;
}
 .aligner {
	 display: flex;
	 align-items: center;
	 justify-content: center;
}
 .title-container {
	 height: 50px;
}
 .title-styles {
	 font-family: 'Raleway', sans-serif;
	 font-size: 250%;
}
 .header-icon {
	 height: 150px;
	 width: 150px;
}
 header {
	 position: relative;
	 height: 520px;
	 min-height: 450px;
	 width: 100%;
	 background-size: cover;
	 -webkit-background-size: cover;
	 text-align: center;
	 overflow: hidden;
	 background-color: #e9d5a1;
	 -webkit-background-size: cover;
	 -moz-background-size: cover;
	 -o-background-size: cover;
	 background-size: cover;
	 padding-top: 150px;
	 padding-bottom: 0;
}
 .language-icon {
	 font-size: 50px;
	 cursor: pointer;
	 margin-right: 15px;
}
 .project-date {
	 font-size: 16px;
	 font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
	 padding: 8px 15px 2px 15px;
	 position: relative;
	 z-index: -500;
	 border-top: 5px solid #696969;
	 border-radius: 0 0 5px 5px;
	 background-color: #696969;
	 color: white;
}
 .skills-tile {
	 background-color: rgba(248, 245, 244, 0.05);
	 padding: 10px 10px 5px 10px;
	 width: 100px;
	 margin: 5px 0 5px 0;
	 border-radius: 8px;
}
 .language {
	 font-size: 25px;
	 background-color: #e9d5a1;
	 padding-bottom: 10px;
	 padding-top: 80px;
}
 header h1 {
	 font-size: 400%;
	 text-align: center;
	 font-weight: 600 !important;
	 color: #353239;
	 font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
	 margin: 0 auto 18px auto;
	 width: 100%;
}
 .slider-image {
	 border: 5px solid #d7caaa;
}
 .slider-tab {
	 background-color: #d7caaa;
	 height: 25px;
}
 .slider-iconfiy {
	 margin-top: 10px;
}
 .styles_typicalWrapper__1_Uvh::after {
	 cursor: none !important;
	 display: none;
}
#whatido {
	background: #fff;
	overflow: hidden;
	padding-bottom: 10%;
}
#whatido h1 {
	padding-top: 5%;
	font: 18px/24px 'opensans-bold', sans-serif;
	text-transform: uppercase;
	letter-spacing: 3px;
	color: black;
	text-align: center;

}
 #about {
	 background: #fff;
	 overflow: hidden;
}
 #about h1 {
	 padding-top: 5%;
	 font: 18px/24px 'opensans-bold', sans-serif;
	 text-transform: uppercase;
	 letter-spacing: 3px;
	 color: black;
	 text-align: center;
}
 .foto img {
	 display: block;
	 max-width: 400px;
	 width: 100%;
}
 .foto div {
	 background: #fff;
	 display: inline-block;
	 margin: 0 auto 5% auto;
	 padding: 10px 10px 5px;
	 text-align: center;
	 text-decoration: none;
	 -webkit-box-shadow: 0 4px 6px rgba(0, 0, 0, .3);
	 -moz-box-shadow: 0 4px 6px rgba(0, 0, 0, .3);
	 box-shadow: 0 4px 6px rgba(0, 0, 0, .3);
	 -webkit-transition: all 0.2s linear;
	 -moz-transition: all 0.2s linear;
	 transition: all 0.2s linear;
	 z-index: 0;
	 position: relative;
}
 .foto div:after {
	 color: #333;
	 font-size: 25px;
	 content: attr(title);
	 position: relative;
	 top: 15px;
}
 .foto div:hover {
	 -webkit-transform: scale(1.01);
	 -moz-transform: scale(1.01);
	 transform: scale(1.01);
	 z-index: 10;
	 -webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, .7);
	 -moz-box-shadow: 0 10px 20px rgba(0, 0, 0, .7);
	 box-shadow: 0 10px 20px rgba(0, 0, 0, .7);
}
 .project-title-settings {
	 margin-top: 5%;
	 font: 18px/24px 'opensans-bold', sans-serif;
	 text-transform: uppercase;
	 letter-spacing: 3px;
	 color: black;
	 text-align: center;
}

