 .polaroid img {
	 display: block;
	 max-width: 200px;
}
 .polaroid span {
	 background: #fff;
	 display: inline-block;
	 margin: 55px 75px 30px;
	 padding: 15px 15px 30px;
	 text-align: center;
	 text-decoration: none;
	 -webkit-box-shadow: 0 4px 6px rgba(0, 0, 0, .3);
	 -moz-box-shadow: 0 4px 6px rgba(0, 0, 0, .3);
	 box-shadow: 0 4px 6px rgba(0, 0, 0, .3);
	 -webkit-transition: all 0.2s linear;
	 -moz-transition: all 0.2s linear;
	 transition: all 0.2s linear;
	 z-index: 0;
	 position: relative;
}
 .link-href {
	 color: black;
}
 .wave {
	 font-size: 160%;
}
 .font-trebuchet {
	 font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
 #preview {
	 width: 500px;
	 background-color: #ebeaf5;
	 padding: 15px;
	 position: relative;
	 margin-bottom: 15%;
}
 .center {
	 display: flex;
	 justify-content: center;
	 align-items: center;
}
 #resume {
	 background: #efe1bd;
}
 .experience-icon {
	 font-size: 300%;
	 margin-top: 25%;
	 text-align: center;
}
 .main-badge {
	 font-size: 13px !important;
	 text-align: left !important;
	 padding: 5px 8px 5px 8px !important;
	 vertical-align: baseline;
	 background-color: #ae944f !important;
	 color: white;
	 font-weight: lighter !important;
	 font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
 .experience-badge {
	 font-size: 11px !important;
	 text-align: left !important;
	 padding: 5px 8px 5px 8px !important;
	 vertical-align: baseline;
	 background-color: #f9f5e9 !important;
	 color: black;
	 font-weight: lighter !important;
	 font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
 .vertical-timeline-element-date {
	 padding: 0 !important;
	 margin: 0 !important;
}
 @media only screen and (max-width: 1170px) {
	 .experience-icon {
		 font-size: 170%;
		 margin-top: 27%;
		 text-align: center;
	}
}
 .modal-inside .modal-content {
	 background: white;
}
 .bars {
	 width: 95%;
	 float: left;
	 padding: 0;
	 text-align: left;
}
 .bars .skills {
	 margin-top: 36px;
	 list-style: none;

}
 .bars li {
	 position: relative;
	 margin-bottom: 60px;
	 background: #ccc;
	 height: 42px;
	 border-radius: 3px;
}
 .bars li em {
	 font: 15px 'opensans-bold', sans-serif;
	 color: #313131;
	 text-transform: uppercase;
	 letter-spacing: 2px;
	 font-weight: normal;
	 position: relative;
	 top: -36px;
}
 .bar-expand {
	 position: absolute;
	 left: 0;
	 top: 0;
	 margin: 0;
	 padding-right: 24px;
	 background: #313131;
	 display: inline-block;
	 height: 42px;
	 line-height: 42px;
	 border-radius: 3px 0 0 3px;
}
 .modal-close {
	 text-align: right;
	 padding: 10px 15px 10px 15px;
	 cursor: pointer;
}
 .close-icon {
	 color: black;
	 font-weight: lighter !important;
}
 .modal-description {
	 text-align: justify;
	 padding: 5px 5px 0 5px;
	 margin-bottom: 20px;
	 font-size: 12px;
}
 .awssld__next {
	 outline: none !important;
}
 .awssld__prev {
	 outline: none !important;
}
 .loader-bar-color {
	 color: black !important;
}
 #portfolio {
	 background: #efe1bd;
	 padding-bottom: 5%;
}
 .portfolio-item {
	 max-width: 100%;
	 margin-bottom: 15px;
	 text-align: center;
}
 .portfolio .portfolio-item .portfolio-item-caption {
	 -webkit-transition: all ease 0.5s;
	 -moz-transition: all ease 0.5s;
	 transition: all ease 0.5s;
	 opacity: 0;
	 background-color: rgba(51, 51, 51, 0.9);
}
 .portfolio .portfolio-item .portfolio-item-caption:hover {
	 opacity: 1;
}
 .portfolio .portfolio-item .portfolio-item-caption .portfolio-item-caption-content {
	 font-size: 1.5rem;
}
 @media (min-width: 576px) {
	 .portfolio .closeButtonResponsive {
		 display: block;
	}
	 .portfolio .portfolio-item {
		 margin-bottom: 30px;
	}

}
 #skills {
	 background: #1f1f1f;
	 min-height: 200px;
	 width: 100%;
	 overflow: hidden;
	 padding-bottom: 10%;
}
 .section-title {
	 padding-top: 5%;
	 padding-bottom: 5%;
	 font: 18px/24px 'opensans-bold', sans-serif;
	 text-transform: uppercase;
	 letter-spacing: 3px;
	 color: #fff;
	 text-align: center;
}
 .skill-icon {
	 font-size: 180%;
	 color: white;
	 text-align: center;
	 position: relative;
}
 footer {
	 background: #1f1f1f;
	 min-height: 100px;
	 width: 100%;
	 overflow: hidden;
	 font-size: 14px;
	 color: white;
	 position: relative;
	 text-align: center;
}
 footer a, footer a:visited {
	 color: #fff;
}
 footer a:hover, footer a:focus {
	 color: #fff;
}
 .social-links {
	 margin-top: 50px;
	 font-size: 22px;
}
 